.product-admin {
  max-width: 800px;
  margin: auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: white;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease-in-out;
  margin: 5px;
}

button:hover {
  background-color: #0056b3;
}

ul {
  list-style-type: none;
  padding: 0;
}

.product-item {
  display: flex;
  align-items: center;
  background-color: #222;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
}

.product-item img {
  border-radius: 5px;
  margin-right: 15px;
}

.product-item h3 {
  margin: 0;
  color: white;
}

.product-item p {
  margin: 5px 0;
  color: #aaa;
}

/* 🎨 Estilos para los modales */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: #333;
  padding: 20px;
  border-radius: 5px;
  width: 400px;
  color: white;
}

.close-button {
  float: right;
  font-size: 24px;
  cursor: pointer;
}

.modal-content form {
  display: flex;
  flex-direction: column;
}

.modal-content form label {
  margin-top: 10px;
  margin-bottom: 5px;
  color: #e0e0e0;
}

.modal-content input,
textarea,
select {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #2c2c2c;
  color: #e0e0e0;
  transition: border-color 0.3s ease;
}

.modal-content button {
  margin: 0;
}

input[type="file"] {
  display: none;
}

button[type="submit"] {
  margin-top: 15px;
}

.upload-button {
  background-color: #28a745;
  margin-top: 10px;
}

.upload-button:hover {
  background-color: #218838;
}

.confirm-button {
  background-color: #dc3545;
}

.confirm-button:hover {
  background-color: #c82333;
}

.cancel-button {
  background-color: #6c757d;
}

.cancel-button:hover {
  background-color: #5a6268;
}